        span 6
      h1 Criterios de sostenibilidad en el turismo: capacidad de carga, impacto ambiental y economía circular

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p En el contexto del turismo étnico sostenible, comprender los criterios de sostenibilidad resulta fundamental para orientar las prácticas turísticas hacia modelos de gestión responsable, inclusiva y resiliente. La sostenibilidad, entendida como el equilibrio entre el desarrollo económico, la equidad social y la conservación ambiental, se consolida como el eje articulador de toda actividad turística que aspire a generar bienestar colectivo y preservar la identidad cultural de los territorios.
      p El turismo, al operar sobre espacios naturales y socioculturales sensibles, requiere herramientas analíticas que permitan evaluar sus efectos y establecer límites que garanticen su viabilidad a largo plazo. En este sentido, la capacidad de carga, el impacto ambiental y la economía circular se constituyen en pilares conceptuales que permiten medir, mitigar y transformar los efectos del turismo sobre los ecosistemas y las comunidades anfitrionas.
      p El estudio de estos criterios no solo busca regular la intensidad del uso turístico o minimizar los daños ambientales, sino también repensar los modelos de producción y consumo dentro del sector, promoviendo prácticas que optimicen los recursos, reduzcan los residuos y fortalezcan las economías locales. Así, la aplicación de estos principios favorece un turismo que reconozca los límites ecológicos, valore la diversidad cultural y fomente una relación equilibrada entre visitantes y comunidades.
      p Es así como se busca promover una mirada crítica y consciente frente a las dinámicas del desarrollo turístico, incentivando la participación activa de las comunidades locales en la toma de decisiones y la adopción de estrategias sostenibles que integren la conservación de los ecosistemas con la calidad de la experiencia del visitante. Por esta razón, resulta esencial comprender e incorporar los conceptos de capacidad de carga, impacto ambiental y economía circular como fundamentos teóricos y operativos del turismo étnico sostenible.
      p
      h3 Capacidad de carga
      p La capacidad de carga turística es un concepto central en la planificación del turismo sostenible, ya que determina los límites de uso que un destino puede soportar sin comprometer su equilibrio ambiental, social y económico. En otras palabras, busca mantener una relación armónica entre la preservación del entorno, la satisfacción del visitante y el bienestar de las comunidades locales.
      p De acuerdo con Cifuentes (1999), la capacidad de carga no se limita a una simple cifra de visitantes, sino que constituye un instrumento de gestión integral que considera múltiples dimensiones:

    TabsA.color-acento-contenido.col-lg-12.justify-content-center(data-aos="fade-up")   
