          .col-xl-10
            h3.letra-acordeon-titulos Capacidad de carga física
            p.letra-acordeon-titulos Cantidad máxima de personas que pueden ocupar un espacio determinado según su tamaño, infraestructura y servicios disponibles.
      
      .tarjeta.p-4(titulo="Capacidad de carga ambiental o ecológica")
        .row
          .col-xl-10
            h3.letra-acordeon-titulos Capacidad de carga ambiental o ecológica
            p.letra-acordeon-titulos Punto a partir del cual los ecosistemas comienzan a mostrar signos de deterioro, como compactación del suelo, erosión, pérdida de flora y fauna o contaminación.
      
      .tarjeta.p-4(titulo="Capacidad de carga social")
        .row
          .col-xl-12
            h3.letra-acordeon-titulos Capacidad de carga social
            p.letra-acordeon-titulos Nivel de visitantes que una comunidad anfitriona puede recibir sin alterar su modo de vida, sus valores culturales o su percepción de bienestar.

      .tarjeta.p-4(titulo="Capacidad de manejo")
        .row
          .col-xl-12
            h3.letra-acordeon-titulos Capacidad de manejo
            p.letra-acordeon-titulos Grado en que las autoridades y administradores del destino cuentan con recursos humanos, técnicos y financieros para controlar y gestionar el flujo turístico de manera adecuada.

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-left")
      p En la práctica, determinar la capacidad de carga implica realizar estudios técnicos y participativos, que integren mediciones ambientales, observaciones sobre el comportamiento del visitante y percepciones comunitarias. Esto permite establecer límites de ingreso, horarios de visita, zonas de acceso restringido o rotación de visitantes, medidas que ayudan a prevenir la sobreexplotación del espacio turístico.
      p Ejemplos exitosos de aplicación de este enfoque se observan en el Parque Nacional Natural Tayrona y el Santuario de Fauna y Flora de Iguaque, donde se establecen cupos máximos diarios de ingreso. Estas medidas no solo protegen los ecosistemas, sino que también mejoran la experiencia del visitante, al evitar aglomeraciones y garantizar un contacto más auténtico con la naturaleza.
      p La Organización Mundial del Turismo (OMT, 2018) y el Ministerio de Comercio, Industria y Turismo de Colombia (MinCIT) recomiendan incorporar el análisis de capacidad de carga en los planes de ordenamiento turístico y en los estudios de impacto ambiental, considerándolo una herramienta esencial para lograr un turismo planificado, sostenible y responsable.
      p
      h3 Impacto ambiental
      p El impacto ambiental del turismo constituye uno de los principales desafíos para la sostenibilidad de los destinos. Si bien esta actividad puede impulsar la conservación y el desarrollo local, también puede convertirse en una fuente significativa de degradación ambiental cuando se gestiona sin planificación, control ni criterios de sostenibilidad.
      p Entre las principales afectaciones ambientales derivadas de una gestión turística inadecuada se encuentran:

    .pb-3(data-aos="fade-left")
