    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p Ante este panorama, los proyectos turísticos deben incorporar evaluaciones de impacto ambiental (EIA) desde su fase de planificación y diseño, con el fin de identificar riesgos, prevenir impactos negativos y establecer estrategias de mitigación y compensación ambiental.
      p Entre las buenas prácticas ambientales recomendadas, se destacan:
    
