    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p La Organización Mundial del Turismo (OMT, 2015) subraya que minimizar la huella ecológica del turismo no solo protege el medio ambiente, sino que también mejora la calidad de la experiencia del visitante y fortalece la competitividad de los destinos.
      p En este sentido, el turismo responsable debe concebirse como una alianza entre conservación, desarrollo y participación comunitaria, en la que la naturaleza no se considere un recurso a explotar, sino un patrimonio común que debe preservarse para las generaciones presentes y futuras.
      p A continuación, se presentan algunos de los impactos ambientales más relevantes y tensionantes relacionados con la industria turística en Colombia, identificados a partir de estudios recientes y reportes institucionales.

    .row.justify-content-center(data-aos="flip-up")
