    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-right")
      p Para mitigar los impactos ambientales generados por la actividad turística, los proyectos de alto impacto que requieran la expedición de una licencia ambiental deben cumplir con lo establecido en la Ley 99 de 1993 y en los lineamientos de la Autoridad Nacional de Licencias Ambientales (ANLA).
      p De acuerdo con esta normativa, es obligatoria la elaboración de un Estudio de Impacto Ambiental (EIA) como requisito previo para la obtención de la licencia. Este estudio debe incluir la identificación, valoración y jerarquización de los impactos que el proyecto pueda generar sobre el medio físico, biótico y social, así como las medidas de prevención, mitigación, corrección y compensación correspondientes.
      p El EIA se constituye, por tanto, en una herramienta técnica y jurídica fundamental para garantizar que el desarrollo de proyectos turísticos se realice bajo criterios de sostenibilidad ambiental, responsabilidad social y cumplimiento normativo, evitando la degradación de los ecosistemas y protegiendo el bienestar de las comunidades locales.      
      p 
      h3 Economía circular
      p La economía circular en el turismo promueve el aprovechamiento eficiente de los recursos, reduciendo el desperdicio y fomentando la reutilización, el reciclaje y la regeneración de materiales y energía. Este enfoque busca cerrar los ciclos productivos y de consumo, minimizando los impactos ambientales y generando valor agregado a lo largo de toda la cadena turística.
      p Entre las iniciativas más representativas se encuentran los hoteles que implementan energías renovables, los restaurantes que priorizan productos locales y de temporada, o las agencias de viajes que digitalizan sus procesos para reducir el uso de papel.
      p Según la Fundación Ellen MacArthur (2019), la economía circular constituye una estrategia clave para la transición hacia modelos turísticos más resilientes, competitivos y sostenibles, al integrar la eficiencia de los recursos con la innovación empresarial y la responsabilidad ambiental
      p 
      h3 Beneficios de la economía circular en el turismo
      p
      p La aplicación de la economía circular al turismo, más allá de responder a una exigencia derivada de la crisis ambiental global, ofrece beneficios tangibles para la industria, entre los cuales se destacan:

